import { useCallback, useEffect, useState } from "react";
import "./Emoji.css";

const backgroundColors = [
    'transparent', '#ffffff', '#bacee0', '#677bac', '#9dcdb8', '#51a5a0', '#9bb157',
    '#f8cd59', '#f99460', '#f68181', '#f7a2bd', '#5b4d49',
    '#d3d5d0', '#525252', '#404372', '#10374a', '#818b9c'
];

const Emoji = () => {
    const [images] = useState(Array.from({ length: 95 }, (_, index) => index + 1));
    const [emoji, setEmoji] = useState(1);
    const [backgroundColor, setBackgroundColor] = useState('#bacee0');

    useEffect(()=>{
        copyToClipboard();
    }, [backgroundColor, emoji]);

    const copyToClipboard = useCallback(async () => {
        try {
            const img = new Image();
            img.crossOrigin = 'anonymous'; // CORS  
            img.src = `${process.env.PUBLIC_URL}/images/emoji/${emoji}.png`;

            img.onload = async () => {
                const canvas = document.createElement('canvas');
                canvas.width = img.width;
                canvas.height = img.height;
                const ctx = canvas.getContext('2d');

                //   (   )
                ctx.fillStyle = backgroundColor;
                //console.log(ctx.fillStyle);
                ctx.fillRect(0, 0, canvas.width, canvas.height);

                ctx.drawImage(img, 0, 0); //    

                canvas.toBlob(async (blob) => {
                    if (blob) {
                        try {
                            const clipboardItem = new ClipboardItem({ 'image/png': blob });
                            await navigator.clipboard.write([clipboardItem]);
                            //alert('  !');
                        } catch (error) {
                            console.error('   :', error);
                        }
                    } else {
                        console.error('Blob  ');
                    }
                }, 'image/png'); // PNG  Blob 
            };

            img.onerror = (error) => {
                console.error('  :', error);
            };
        } catch (error) {
            console.error('     :', error);
        }
    }, [backgroundColor]);

    return (<>
        <div className="row">
            <div className="col">
                <h1> </h1>
                <hr></hr>
                <p>    ctrl+v !</p>
            </div>
        </div>

        <div className="row mt-4">
            <div className="col">
                <h4> (ex :   )</h4>
                {backgroundColors.map((color, index)=>(
                <div className={`color-palette${color === backgroundColor ? ' on' : ''}`} style={{backgroundColor:color}}
                    key={index} onClick={e=>setBackgroundColor(color)}>{color === 'transparent' && <span></span>}</div>
                ))}
            </div>
        </div>

        <div className="row mt-4">
            <div className="col">
                <h4> </h4>
                {images.map(imageNo => (
                    <img className={`topwar-emoji${emoji === imageNo ? ' on':''}`} src={`${process.env.PUBLIC_URL}/images/emoji/${imageNo}.png`}
                        width={50} height={50} key={imageNo}
                        onClick={e => setEmoji(imageNo)} />
                ))}
            </div>
        </div>
    </>);
};

export default Emoji;