import { NavLink } from "react-router-dom";

function Menu() {
    return (
        <nav className="navbar navbar-expand-lg bg-primary fixed-top" data-bs-theme="dark">
            <div className="container-fluid">
                <NavLink className="navbar-brand" to="/">Topwar Helper</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarColor01">
                    <ul className="navbar-nav me-auto"> 
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false"></a>
                            <div className="dropdown-menu">
                                <NavLink className="dropdown-item" to="/information/base"> </NavLink>
                                {/* <NavLink className="dropdown-item" to="/information/decor"> </NavLink> */}
                                <NavLink className="dropdown-item" to="/information/hero"> </NavLink>
                                <div className="dropdown-divider"></div>
                                <NavLink className="dropdown-item" to="/information/job">  </NavLink>
                                {/* <a className="dropdown-item">Separated link</a> */}
                            </div>
                        </li>
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false"></a>
                            <div className="dropdown-menu">
                                <NavLink className="dropdown-item" to="/calculator/vital">   </NavLink>
                                <NavLink className="dropdown-item" to="/calculator/gathering">  </NavLink>
                                <NavLink className="dropdown-item" to="/calculator/skill">  </NavLink>
                                {/* <div className="dropdown-divider"></div>/ */}
                                {/* <a className="dropdown-item">Separated link</a> */}
                            </div>
                        </li>
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false"></a>
                            <div className="dropdown-menu">
                                <NavLink className="nav-link" to="/simulator/random"></NavLink>
                                <NavLink className="nav-link" to="/simulator/hero"></NavLink>
                            </div>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/emoji"></NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/developer"></NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default Menu;