import { useCallback, useState } from "react";

function RandomSimulator() {
    //item list
    const [sequence, setSequence] = useState(1);
    const [itemList, setItemList] = useState([]);
    const addItemList = useCallback(()=>{
        const newList = [...itemList, {no:sequence, name:"", rate:0, count:0, acc:0}];
        setItemList(newList);
        setSequence(sequence+1);
    }, [itemList]);
    const deleteItemList = useCallback(()=>{
        setItemList(itemList.filter((item,idx)=>idx < itemList.length-1));
    },  [itemList]);

    //count
    const [count, setCount] = useState(1);

    const inputItem = useCallback((e)=>{
        const no = parseInt(e.target.dataset.no);
        const name = e.target.name;
        const value = name === "rate" ? parseInt(e.target.value) : e.target.value;
        setItemList(prev=>prev.map(item=>{
            if(item.no === no) {
                return {...item, [name]:value};
            }
            else {
                return item;
            }
        }));
    }, [itemList]);

    const numberWithCommas = useCallback((x)=>{
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }, []);

    //history
    const [history, setHistory] = useState([]);

    const lot = useCallback(()=>{
        if(itemList.length === 0 || count === 0) {
            window.alert("  ");
            return;
        }

        const totalRate = itemList.reduce((b,c)=>{
            return b+c.rate;
        },0);

        if(totalRate !== 100) {
            window.alert(" 100% ");
            return;
        }

        if(count > 10000 || count <= 0) {
            window.alert("1~10000  ");
            return;
        }

        //
        let acc = 0;
        const copyList = itemList.map(item=>{
            acc += parseInt(item.rate * 10000);
            return {...item, acc:acc, count:0};
        });
        
        const array = new Uint32Array(count);
        window.self.crypto.getRandomValues(array);
        const result = array.map(n=>n%1000000);

        setHistory([]);
        result.forEach((res,idx)=>{
            for(let i=0; i < copyList.length; i++) {
                const copyItem = copyList[i];
                if(copyItem.acc > res) {
                    copyItem.count++;
                    setHistory(prev=>[...prev, copyItem]);
                    break;
                }
            }

        });

        setItemList(copyList);
    }, [itemList, count]);

    

    return (
        <>
            <div className="row">
                <div className="col">
                    <h1> </h1>
                    <hr></hr>
                </div>
            </div>

            <div className="row">
                <div className="col-12">
                    <h2>
                          &nbsp;&nbsp;
                        <button className="btn btn-primary" onClick={addItemList}>+ </button>
                        &nbsp;&nbsp;
                        <button className="btn btn-danger" onClick={deleteItemList}>- </button>
                    </h2>
                    <p><small>     <br/>(   100% )</small></p>
                </div>
                <div className="col-12">
                    {itemList.map((item,index)=>(
                        <div key={index} className="mt-2 p-2 shadow-sm rounded">
                            <label>  <input type="text" name="name" onChange={inputItem} placeholder="(ex)   " data-no={item.no} defaultValue={item.name}/></label>
                            &nbsp;&nbsp;&nbsp;
                            <label>  <input type="number" name="rate" onChange={inputItem} placeholder="%  " data-no={item.no} defaultValue={item.rate}/> %</label>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        </div>
                    ))}
                </div>
            </div>

            <div className="row mt-4">
                <div className="col-12">
                    <h2>
                         
                    </h2>
                    <p><small>
                         &nbsp;
                        <span className="text-danger">1</span> &nbsp;
                        <span className="text-danger">10000</span>    
                    </small></p>
                </div>
                <div className="col-12 text-end">
                    <input type="number" min="1" defaultValue={count} onChange={e=>setCount(parseInt(e.target.value))} className="text-end"/> 
                </div>
            </div>
            <div className="row mt-2">
                <div className="col">
                    <button className="btn btn-success w-100" onClick={lot}></button>
                </div>
            </div>
            <div className="row mt-4">
                <div className="col-12">
                    <h2> </h2>
                </div>
                <div className="col-12">
                    {itemList.map((item,index)=>(
                        <div key={index} className="mt-2 p-2 shadow-sm rounded d-flex">
                            <div className="w-50">
                                <span>{item.name}</span>&nbsp;&nbsp;
                                <span className="text-muted">({item.rate}%)</span> 
                            </div>
                            <div className="w-50 text-end">
                                <span className="text-danger"><b>{numberWithCommas(item.count)}</b></span>  
                            </div>
                        </div>
                    ))}
                </div>
                <div className="col-12 mt-2">
                    {history.map((h,i)=>(
                        <div className="ps-2 pe-2" key={i}>
                            <span className="badge bg-primary me-2" style={{width:55}}>{i+1}</span>
                            <b className="text-danger">{h.name}</b>  !
                        </div>
                    ))}
                </div>
            </div>
        </>
    );
}

export default RandomSimulator;